				<el-form-item :style='{"width":"48%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' class="input" v-if="type!='info'"  label="用户余额" prop="yonghuyue">
					<el-input-number v-model="ruleForm.yonghuyue" placeholder="用户余额" :readonly="ro.yonghuyue"></el-input-number>
				</el-form-item>
				<el-form-item :style='{"width":"48%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' v-else class="input" label="用户余额" prop="yonghuyue">
					<el-input v-model="ruleForm.yonghuyue" placeholder="用户余额" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"48%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' class="input" v-if="type!='info'"  label="订阅备注" prop="dingyuebeizhu">
					<el-input v-model="ruleForm.dingyuebeizhu" placeholder="订阅备注" clearable  :readonly="ro.dingyuebeizhu"></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"48%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' v-else class="input" label="订阅备注" prop="dingyuebeizhu">
					<el-input v-model="ruleForm.dingyuebeizhu" placeholder="订阅备注" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"48%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' class="date" v-if="type!='info'" label="下单时间" prop="xiadanshijian">
					<el-date-picker
						value-format="yyyy-MM-dd HH:mm:ss"
						v-model="ruleForm.xiadanshijian" 
						type="datetime"
						:readonly="ro.xiadanshijian"
						placeholder="下单时间"
					></el-date-picker>
				</el-form-item>
				<el-form-item :style='{"width":"48%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' class="input" v-else-if="ruleForm.xiadanshijian" label="下单时间" prop="xiadanshijian">
					<el-input v-model="ruleForm.xiadanshijian" placeholder="下单时间" readonly></el-input>
				</el-form-item>
			</template>
			<el-form-item :style='{"padding":"0","margin":"30px 0","alignItems":"center","textAlign":"center","display":"flex","width":"100%","perspective":"320px","-webkitPerspective":"320px","fontSize":"48px","justifyContent":"flex-start"}' class="btn">
				<el-button class="btn3"  v-if="type!='info'" type="success" @click="onSubmit">
					<span class="icon iconfont icon-tijiao16" :style='{"margin":"0 2px","fontSize":"18px","color":"inherit","display":"none"}'></span>
					提交
				</el-button>
				<el-button class="btn4" v-if="type!='info'" type="success" @click="back()">
					<span class="icon iconfont icon-quxiao09" :style='{"margin":"0 2px","fontSize":"18px","color":"inherit","display":"none"}'></span>
					取消
				</el-button>
				<el-button class="btn5" v-if="type=='info'" type="success" @click="back()">
					<span class="icon iconfont icon-fanhui01" :style='{"margin":"0 2px","fontSize":"18px","color":"inherit","display":"none"}'></span>
					返回
				</el-button>
			</el-form-item>
		</el-form>
    

  </div>
</template>
<script>
// 数字，邮件，手机，url，身份证校验
import { isNumber,isIntNumer,isEmail,isPhone, isMobile,isURL,checkIdCard } from "@/utils/validate";
export default {
	data() {
		let self = this
		var validateIdCard = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!checkIdCard(value)) {
				callback(new Error("请输入正确的身份证号码"));
			} else {
				callback();
			}
		};
		var validateUrl = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isURL(value)) {
				callback(new Error("请输入正确的URL地址"));
			} else {
				callback();
			}
		};
		var validateMobile = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isMobile(value)) {
				callback(new Error("请输入正确的手机号码"));
			} else {
				callback();
			}
		};
		var validatePhone = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isPhone(value)) {
				callback(new Error("请输入正确的电话号码"));
			} else {
				callback();
			}
		};
		var validateEmail = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isEmail(value)) {
				callback(new Error("请输入正确的邮箱地址"));
			} else {
				callback();
			}
		};
		var validateNumber = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isNumber(value)) {
				callback(new Error("请输入数字"));
			} else {
				callback();
			}
		};
		var validateIntNumber = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isIntNumer(value)) {
				callback(new Error("请输入整数"));
			} else {
				callback();
			}
		};
		return {
			id: '',
            uObject: null,
			type: '',
			
			
			ro:{
				dingyuebianhao : false,
				changdimingcheng : false,
				changditupian : false,
				changdifenlei : false,
				changdiweizhi : false,
				dingyueshizhang : false,
				feiyong : false,
				yue : false,
				dingyueshijian : false,
				guihaishijian : false,
				zhanghao : false,
				xingming : false,
				shoujihaoma : false,
				yonghuyue : false,
				dingyuebeizhu : false,
				xiadanshijian : false,
				sfsh : false,
				shhf : false,
			},
			
			
			ruleForm: {
				dingyuebianhao: this.getUUID(),
				changdimingcheng: '',
				changditupian: '',
				changdifenlei: '',
				changdiweizhi: '',
				dingyueshizhang: '',
				feiyong: '',
				yue: '',
				dingyueshijian: '',
				guihaishijian: '',
				zhanghao: '',
				xingming: '',
				shoujihaoma: '',
				yonghuyue: '',
				dingyuebeizhu: '',
				xiadanshijian: '',
				shhf: '',
			},
		

            dingyueshijianPickerOptions: {
                disabledDate(time) {
                    return time.getTime() < Date.now() - 8.64e7;
                }
            },
            guihaishijianPickerOptions: {
                disabledDate(time) {
                    return time.getTime() < Date.now() - 8.64e7;
                }
            },
			
			rules: {
				dingyuebianhao: [
					{ required: true, message: '订阅编号不能为空', trigger: 'blur' },
				],
				changdimingcheng: [
				],
				changditupian: [
				],
				changdifenlei: [
				],
				changdiweizhi: [
				],
				dingyueshizhang: [
					{ validator: validateIntNumber, trigger: 'blur' },
				],
				feiyong: [
					{ validator: validateNumber, trigger: 'blur' },
				],
				yue: [
					{ validator: validateNumber, trigger: 'blur' },
				],
				dingyueshijian: [
					{ required: true, message: '订阅时间不能为空', trigger: 'blur' },
				],
				guihaishijian: [
					{ required: true, message: '归还时间不能为空', trigger: 'blur' },
				],
				zhanghao: [
				],
				xingming: [
				],
				shoujihaoma: [
				],
				yonghuyue: [
					{ validator: validateNumber, trigger: 'blur' },
				],
				dingyuebeizhu: [
				],
				xiadanshijian: [
				],
				sfsh: [
				],
				shhf: [
				],
			}
		};
	},
	props: ["parent"],
	computed: {
		dingyueshizhang : {
			get: function () {
				let h = this.ruleForm
				let a = 'h.dingyueshijian-h.guihaishijian'
				let n = a.split('-')
				let hour = this.getHour(h[n[0].split('h.')[1]], h[n[1].split('h.')[1]])
				this.ruleForm.dingyueshizhang = hour?hour:0
				return hour?hour:0
			}
			
		},


		yue:{
			get: function () {
				return 1*this.ruleForm.dingyueshizhang*this.ruleForm.feiyong
			}
		},

	},
    components: {
    },
	created() {
		this.ruleForm.xiadanshijian = this.getCurDateTime()
	},
	methods: {
		// 获取时间间隔 单位小时
		getHour(first, last){
			let date1 = new Date(first)
			let date2 = new Date(last)
			let a = date1.getTime();
			let b = date2.getTime();
			var count = 0;
			for (var i = a; i < b; i += 3600 * 1000) {
				count++;
			}
			return count;
		},
		
		// 下载
		download(file){
			window.open(`${file}`)
		},
		// 初始化
		init(id,type) {
			if (id) {
				this.id = id;
				this.type = type;
			}
			if(this.type=='info'||this.type=='else'){
				this.info(id);
			}else if(this.type=='logistics'){
				this.logistics=false;
				this.info(id);
			}else if(this.type=='cross'){
				var obj = this.$storage.getObj('crossObj');
				for (var o in obj){
						if(o=='dingyuebianhao'){
							this.ruleForm.dingyuebianhao = obj[o];
							this.ro.dingyuebianhao = true;
							continue;
						}
						if(o=='changdimingcheng'){
							this.ruleForm.changdimingcheng = obj[o];
							this.ro.changdimingcheng = true;
							continue;
						}
						if(o=='changditupian'){
							this.ruleForm.changditupian = obj[o];
							this.ro.changditupian = true;
							continue;
						}
						if(o=='changdifenlei'){
							this.ruleForm.changdifenlei = obj[o];
							this.ro.changdifenlei = true;
							continue;
						}
						if(o=='changdiweizhi'){
							this.ruleForm.changdiweizhi = obj[o];
							this.ro.changdiweizhi = true;
							continue;
						}
						if(o=='dingyueshizhang'){
							this.ruleForm.dingyueshizhang = obj[o];
							this.ro.dingyueshizhang = true;
							continue;
						}
						if(o=='feiyong'){
							this.ruleForm.feiyong = obj[o];
							this.ro.feiyong = true;
							continue;
						}
						if(o=='yue'){
							this.ruleForm.yue = obj[o];
							this.ro.yue = true;
							continue;
						}
						if(o=='dingyueshijian'){
							this.ruleForm.dingyueshijian = obj[o];
							this.ro.dingyueshijian = true;
							continue;
						}
						if(o=='guihaishijian'){
							this.ruleForm.guihaishijian = obj[o];
							this.ro.guihaishijian = true;
							continue;
						}
						if(o=='zhanghao'){
							this.ruleForm.zhanghao = obj[o];
							this.ro.zhanghao = true;
							continue;
						}
						if(o=='xingming'){
							this.ruleForm.xingming = obj[o];
							this.ro.xingming = true;
							continue;
						}
						if(o=='shoujihaoma'){
							this.ruleForm.shoujihaoma = obj[o];
							this.ro.shoujihaoma = true;
							continue;
						}
						if(o=='yonghuyue'){
							this.ruleForm.yonghuyue = obj[o];
							this.ro.yonghuyue = true;
							continue;
						}
						if(o=='dingyuebeizhu'){
							this.ruleForm.dingyuebeizhu = obj[o];
							this.ro.dingyuebeizhu = true;
							continue;
						}
						if(o=='xiadanshijian'){
							this.ruleForm.xiadanshijian = obj[o];
							this.ro.xiadanshijian = true;
							continue;
						}
				}
				


















			}
			
			// 获取用户信息
			this.$http({
				url: `${this.$storage.get('sessionTable')}/session`,
				method: "get"
			}).then(({ data }) => {
				if (data && data.code === 0) {
					
					var json = data.data;
                    this.uObject = data.data;
					if(this.$storage.get("role")!="管理员") {
						this.ro.dingyueshizhang = true;
					}
					if(this.$storage.get("role")!="管理员") {
						this.ro.feiyong = true;
					}
					if(((json.zhanghao!=''&&json.zhanghao) || json.zhanghao==0) && this.$storage.get("role")!="管理员"){
						this.ruleForm.zhanghao = json.zhanghao
						this.ro.zhanghao = true;
					}
					if(((json.xingming!=''&&json.xingming) || json.xingming==0) && this.$storage.get("role")!="管理员"){
						this.ruleForm.xingming = json.xingming
						this.ro.xingming = true;
					}
					if(((json.shoujihaoma!=''&&json.shoujihaoma) || json.shoujihaoma==0) && this.$storage.get("role")!="管理员"){
						this.ruleForm.shoujihaoma = json.shoujihaoma
						this.ro.shoujihaoma = true;
					}
					if(((json.yue!=''&&json.yue) || json.yue==0) && this.$storage.get("role")!="管理员"){
						this.ruleForm.yonghuyue = json.yue
						this.ro.yonghuyue = true;
					}
				} else {
					this.$message.error(data.msg);
				}
			});
			
			
		},
    // 多级联动参数

    info(id) {
      this.$http({
        url: `dingyuexinxi/info/${id}`,
        method: "get"
      }).then(({ data }) => {
        if (data && data.code === 0) {
        this.ruleForm = data.data;
        //解决前台上传图片后台不显示的问题
        let reg=new RegExp('../../../upload','g')//g代表全部
        } else {
          this.$message.error(data.msg);
        }
      });
    },


    // 提交
    onSubmit() {
		if(this.ruleForm.dingyueshizhang==0){
			this.$message.error('订阅时长不能为空')
			return false
		}
		if(this.ruleForm.dingyuebianhao) {
			this.ruleForm.dingyuebianhao = String(this.ruleForm.dingyuebianhao)
		}
        this.ruleForm.yue = this.yue



	if(this.ruleForm.changditupian!=null) {
		this.ruleForm.changditupian = this.ruleForm.changditupian.replace(new RegExp(this.$base.url,"g"),"");
	}










      if(parseFloat(this.ruleForm.yue)>parseFloat(this.ruleForm.yonghuyue)){
        this.$message.error("应付金额不能超过用户余额");
        return
      }






var objcross = this.$storage.getObj('crossObj');
      //更新跨表属性
       var crossuserid;
       var crossrefid;
       var crossoptnum;
       if(this.type=='cross'){
                var statusColumnName = this.$storage.get('statusColumnName');
                var statusColumnValue = this.$storage.get('statusColumnValue');
                if(statusColumnName!='') {
                        var obj = this.$storage.getObj('crossObj');
                       if(statusColumnName && !statusColumnName.startsWith("[")) {
                               for (var o in obj){
                                 if(o==statusColumnName){
                                   obj[o] = statusColumnValue;
                                 }
                               }
                               var table = this.$storage.get('crossTable');
                             this.$http({
                                 url: `${table}/update`,
                                 method: "post",
                                 data: obj
                               }).then(({ data }) => {});
                       } else {
                               crossuserid=this.$storage.get('userid');
                               crossrefid=obj['id'];
                               crossoptnum=this.$storage.get('statusColumnName');
                               crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
                        }
                }
        }
		this.$refs["ruleForm"].validate(valid => {
			if (valid) {
				if(crossrefid && crossuserid) {
					this.ruleForm.crossuserid = crossuserid;
					this.ruleForm.crossrefid = crossrefid;
					let params = { 
						page: 1, 
						limit: 10, 
						crossuserid:this.ruleForm.crossuserid,
						crossrefid:this.ruleForm.crossrefid,
					} 
				this.$http({ 
					url: "dingyuexinxi/page", 
					method: "get", 
					params: params 
				}).then(({ 
					data 
				}) => { 
					if (data && data.code === 0) { 
						if(data.data.total>=crossoptnum) {
							this.$message.error(this.$storage.get('tips'));
							return false;
						} else {
							this.$http({
								url: `dingyuexinxi/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(({ data }) => {
								if (data && data.code === 0) {
									if(this.uObject.yue>=0){
										this.uObject.yue = parseFloat(this.uObject.yue) - parseFloat(this.ruleForm.yue)
										this.$http({url: `${this.$storage.get('sessionTable')}/update`,method: "post",data: this.uObject}).then(res => {});
									}
									this.$message({
										message: "操作成功",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.dingyuexinxiCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});

						}
					} else { 
				} 
			});
		} else {
			this.$http({
				url: `dingyuexinxi/${!this.ruleForm.id ? "save" : "update"}`,
				method: "post",
			   data: this.ruleForm
			}).then(({ data }) => {
				if (data && data.code === 0) {
					if(this.uObject.yue>=0){
						this.uObject.yue = parseFloat(this.uObject.yue) - parseFloat(this.ruleForm.yue)
						this.$http({url: `${this.$storage.get('sessionTable')}/update`,method: "post",data: this.uObject}).then(res => {});
					}
					this.$message({
						message: "操作成功",
						type: "success",
						duration: 1500,
						onClose: () => {
							this.parent.showFlag = true;
							this.parent.addOrUpdateFlag = false;
							this.parent.dingyuexinxiCrossAddOrUpdateFlag = false;
							this.parent.search();
							this.parent.contentStyleChange();
						}
					});
				} else {
					this.$message.error(data.msg);
			   }
			});
		 }
         }
       });
    },
    // 获取uuid
    getUUID () {
      return new Date().getTime();
    },
    // 返回
    back() {
      this.parent.showFlag = true;
      this.parent.addOrUpdateFlag = false;
      this.parent.dingyuexinxiCrossAddOrUpdateFlag = false;
      this.parent.contentStyleChange();
    },
    changditupianUploadChange(fileUrls) {
	    this.ruleForm.changditupian = fileUrls;
    },
  }
};
